
import React from 'react';
import { PropTypes } from 'prop-types';
import ReactAudioPlayer from 'react-audio-player';
import './assets/materialize.min.css';
import './assets/materialize.min';
import './assets/style.css';

class AudioSearch extends React.Component {
  constructor(props) {
    super(props);
    this.jumpToTime = this.jumpToTime.bind(this);
    this.performSearch = this.performSearch.bind(this);
    this.findMatch = this.findMatch.bind(this);
    this.playPause = this.playPause.bind(this);
    this.onSearch = this.onSearch.bind(this);

    this.state = {
      searchTerm: '',
      matches: [],
      isPlaying: false,
      currentTime: 0
    };
  }

  componentDidMount() {
    const audio = this.rap.audioEl;
    audio.addEventListener('timeupdate', () => {
      const currentTime = parseInt(audio.currentTime, 10);
      this.setState({
        currentTime
      });
    });

    audio.addEventListener('canplay', () => {
      this.setState({
        duration: audio.duration
      });
    });
  }

  playPause() {
    if (this.state.isPlaying) {
      this.rap.audioEl.pause();
    } else {
      this.rap.audioEl.play();
    }
    this.setState({
      isPlaying: !this.state.isPlaying
    });
  }

  jumpToTime(time, index) {
    this.rap.audioEl.currentTime = time;
    this.setState({
      currentTime: time,
      isPlaying: true
    });
    this.rap.audioEl.play();
  }

  onChange(event) {
    this.setState({
      [event.target.name]: event.target.value
    });
  }

  onSearch(event) {
    this.onChange(event);
    this.performSearch(event.target.value);
  }

  onSeek(event) {
    this.rap.audioEl.currentTime = event.target.value;
    this.setState({
      [event.target.name]: event.target.value,
      isPlaying: true
    });
    this.rap.audioEl.play();
  }

  performSearch(value) {
    this.setState({
      matches: []
    });

    if (value.length > 3) {
      fetch(this.props.transcript)
        .then(response =>
          response.text())
        .then((response) => {
          this.findMatch(response);
        });
    }
  }

  findMatch(text) {
    const { matches } = this.state;
    const foundMatch = text.match(new RegExp(`(.*):.*${this.state.searchTerm}`));

    if (text.indexOf(this.state.searchTerm) === 0) {
      matches.push({ time: 0, value: this.state.searchTerm });
      this.setState({
        matches
      });
      return true;
    }

    if (foundMatch) {
      const value = text.match(new RegExp(`${foundMatch[1]}: (.*)`));
      matches.push({ time: foundMatch[1], value: value[1].replace(this.state.searchTerm, `<strong>${this.state.searchTerm}</strong>`) });
      return this.findMatch(text.replace(this.state.searchTerm, ''));
    }

    this.setState({
      matches
    });
    return true;
  }

  showMatch(text) {
    return { __html: text };
  }

  render() {
    const rangeLength = 1033.5;
    const offset = 41;

    return (
      <div className="container">
        <div className="search-box-container">
          <input
            type="search"
            value={this.state.searchTerm}
            name="searchTerm"
            className="search-input"
            onChange={event => this.onSearch(event)}
            placeholder="Type your search term (not less than 4 characters)"
          />
        </div>
        <ReactAudioPlayer
          ref={(element) => { this.rap = element; }}
          src={this.props.audio}
        />
        <div className="row">
          <div className="col m1 l1">
            <button
              onClick={this.playPause}
              className="btn-floating btn-small waves-effect waves-light green play-pause-btn"
            >
              <i className="material-icons">{this.state.isPlaying ? 'pause_circle_filled' : 'play_arrow'}</i>
            </button>
          </div>
          <div className="col m11 l11">
            <form action="#">
              <p className="range-field">
                <input type="range" className="seek" name="currentTime" onChange={event => this.onSeek(event)} value={this.state.currentTime} max={this.state.duration} min="0" />
              </p>
            </form>
          </div>
        </div>
        <div className="row">
          <div className="col m1 l1" />
          <div className="col m11 l11">
            <div className="markers">
              { this.state.matches.map((jumpTo, index) =>
                (
                  <div className="marker-container" key={index}>
                    <input
                      type="range"
                      className="shadow-seek browser-default"
                      value={jumpTo.time}
                      max={this.state.duration}
                      readOnly
                      onClick={() => this.jumpToTime(jumpTo.time, index)}
                    />
                    <button
                      className={`label ${(jumpTo.isFocused) ? 'isFocused' : ''}`}
                      style={{ left: `${((jumpTo.time / this.state.duration) * rangeLength) - (offset + index)}px` }}
                    >
                      <span dangerouslySetInnerHTML={this.showMatch(jumpTo.value)} /> <br />
                      <strong>{jumpTo.time}</strong>
                    </button>
                  </div>
                ))
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AudioSearch.propTypes = {
  audio: PropTypes.string.isRequired,
  transcript: PropTypes.string.isRequired
};

export default AudioSearch;



